<template>
	<div class="searchWrapper">
		<div class="search-box-wrap">
			<searchbox ref="searchbox" @query="onQueryChange"></searchbox>
		</div>
		<div class="hot-key-wrap">
			<div class="shot-cut" v-show="!query">
				<div class="hot-key">
					<h1 class="title">热门搜索</h1>
					<ul>
						<li class="item" v-for="el in hotkey" @click="addQuery(el.k)">
							{{el.k}}
						</li>
					</ul>
				</div>
			</div>
			<div class="suggest-box" v-show="query">
				<suggest :query="query"></suggest>
			</div>
		</div>
		<router-view></router-view>
	</div>
</template>
<script type="text/ecmascript-6">
	import searchbox from 'components/base/search-box/search-box'
	import {getHotKey} from 'api/search'
	import {ERR_OK} from 'api/config'
	import suggest from 'components/suggest/suggest'
	export default {
		created() {
			this._getHotKey()
		},
		data() {
			return {
				hotkey: [],
				query: ''
			}
		},
		methods: {
			_getHotKey() {
				getHotKey().then((res) => {
					if (res.code === ERR_OK) {
						this.hotkey = res.data.hotkey.splice(0, 10)
					}
				})
			},
			addQuery(query) {
				this.$refs.searchbox.setQuery(query)
			},
			onQueryChange(query) {
				this.query = query
			}
		},
		components: {
			searchbox,
			suggest
		}
	}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
@import '~common/stylus/variable.styl'

	.searchWrapper
		position: fixed
		width: 100%
		top: 89px
		bottom: 0
		padding:0 15px
		box-sizing: border-box
		overflow: hidden
		.hot-key-wrap
			position:fixed
			top:150px
			bottom:0
			right:15px
			left:15px
			.shot-cut
				height:100%
				overflow:hidden
				.hot-key
					.title
						margin-bottom: 15px
						font-size: $font-size-medium
						color: $color-text-l
					.item
						display:inline-block
						padding:5px 10px
						margin:0 15px 10px 0
						border-radius:6px
						background-color:$color-background-l
						font-size:$font-size-small-s
						color:$color-text-l
			.suggest-box
				width:100%
				height:100%
				overflow:hidden
</style>



